* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    font-family: 'Fira Sans', sans-serif;
}

a {
    text-decoration: none;
}

header {
    padding-top: 50px;
    padding-bottom: 50px;
}

header h1 {
    color: black;
    font-size: 42px;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 30px;
}

header strong {
    color: rgb(211, 175, 14);
}

body {
    background-color: #888;
}

/*---SIDEBAR------------------------------------------------------------------------------------*/

.content-wrap {
    display: flex;
}

.content-wrap .sidebar1 {
    flex-direction: column;
    max-width: 250px;
    padding-right: 1rem;
    border-style: dotted;
}

.content-wrap .sidebar1 nav {
    padding-left: 1rem;
    padding-right: 1rem;
}

.content-wrap .sidebar1 nav h3 {
    color: black;
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.content-wrap .sidebar1 nav strong {
    color: rgb(211, 175, 14);
}

.content-wrap .sidebar1 nav a {
    display: block;
    padding: 0.5rem 1rem;
    text-align: left;
    color: black;
    font-size: 1rem;
    margin-bottom: 1rem;
    background-color: #EEE;
    border-radius: 999px;
    text-decoration: none;
    transition: 0.4s;
}

.content-wrap .sidebar1 nav a:hover {
    background-color: #313131;
    color: #AAA;
}

/*---SIDEBAR2------------------------------------------------------------------------------------*/

.content-wrap .sidebar2 {
    flex-direction: column;
    max-width: 250px;
    padding-right: 1rem;
    border-style: dotted;
}

.content-wrap .sidebar2 nav {
    padding-left: 1rem;
    padding-right: 1rem;
}

.content-wrap .sidebar2 nav h3 {
    color: black;
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.content-wrap .sidebar2 nav strong {
    color: rgb(211, 175, 14);
}

.content-wrap .sidebar2 nav a {
    display: block;
    padding: 0.5rem 1rem;
    text-align: left;

    color: black;
    font-size: 1rem;
    margin-bottom: 1rem;

    background-color: #EEE;
    border-radius: 999px;
    transition: 0.4s;
}

.content-wrap .sidebar2 nav a:hover {
    background-color: #313131;
    color: #AAA;
}

/*--MAIN CONT------------------------------------------------------------------------------------*/
/*--Anime CONT-----------------------------------------------------------------------------------*/


.content-wrap main {
    flex: 1 1 0%;
    padding: 0px 8px;
}

.content-wrap main .main-head {
    display: flex;
    justify-content: flex-end;
}

.content-wrap main .main-head .sok-box {
    display: flex;
    justify-content: flex-end;
    flex: 1 1 0%;
}

.content-wrap main .main-head .sok-box2 {
    display: flex;
    justify-content: flex-end;
    flex: 1 1 0%;
}

.content-wrap main form h3 {
    display: flex;
    justify-content: flex-end;
    flex: 0.1 1 10%;
}

.content-wrap main .main-head .sok-box input {
    appearance: none;
	background: none;
	outline: none;
	border: none;
	display: block;
	width: 100%;
	max-width: 600px;
	padding: 1rem;
    border-radius: 999px;
	background-color: #EEE;
    transition: 0.4s;
}

.content-wrap main .main-head .sok-box2 input {
    appearance: none;
	background: none;
	outline: none;
	border: none;
	display: block;
	width: 100%;
	max-width: 600px;
	padding: 1rem;
    border-radius: 999px;
	background-color: #EEE;
    transition: 0.4s;
}

.content-wrap main .main-head .sok-box input ::placeholder {
    color: #888;
}

.content-wrap main .main-head .sok-box2 input ::placeholder {
    color: #888;
}

.content-wrap main .main-head .sok-box input:focus {
    background-color: #313131;
    color: #FFF;
}

.content-wrap main .main-head .sok-box2 input:focus {
    background-color: #313131;
    color: #FFF;
}

.content-wrap main .ani-lista {
    display: flex;
    flex-wrap: wrap;
	margin-top: 2rem;
	margin: 32px -8px 0px;
}

.content-wrap main .ani-lista .ani-kort {
    flex: 1 1 33.333%;
    max-width: 33.333%;
    padding: 1rem 0.5rem;
}

.content-wrap main .ani-lista .ani-kort a {
    text-decoration: none;
}

.content-wrap main .ani-lista .ani-kort figure {
    display: block;
    margin-bottom: 1rem;
}

.content-wrap main .ani-lista .ani-kort figure img {
    display: block;
    width: 100%;
    height: 350px;
    object-fit: cover;
    border-radius: 1rem;
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.15);
    transition: 0.4s;
}

.content-wrap main .ani-lista .ani-kort h3 {
    color: #313131;
}

.content-wrap main .ani-lista .ani-kort:hover figure img {
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.25);
    transform: scale(1.05);
}

